* {
  padding: 0;
  box-sizing: border-box;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
}
body {
  width: 100% !important;
  min-height: 100vh !important;
  background: #e5e5e5 !important;
}
.App {
  width: 400px;
  min-height: 500px;
  position: absolute;
  top: 50px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 20px;
  box-shadow: 10px 10px 13px rgba(0, 0, 0, 0.13),
    -10px -10px 13px hsla(0, 0%, 100%, 0.47);
}
form {
  margin: 20px 0;
  height: 40px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
form button,
form input {
  width: 250px;
  height: 100%;
  border: none;
  outline: none;
  background: transparent;
  border-radius: 5px;
  padding: 0 5px;
  box-shadow: inset -5px -5px 5px hsla(0, 0%, 100%, 0.47),
    inset 5px 5px 5px rgba(0, 0, 0, 0.13);
}
form button,
form button:focus {
  outline: none;
  width: 80px;
  margin-left: 15px;
  letter-spacing: 2px;
  cursor: pointer;
  box-shadow: -5px -5px 5px hsla(0, 0%, 100%, 0.47),
    5px 5px 5px rgba(0, 0, 0, 0.13);
}
h1 {
  text-align: center;
  font-size: 2.5em;
  text-transform: uppercase;
  color: #e5e5e5;
  margin-bottom: 20px;
  text-shadow: -3px -3px 3px hsla(0, 0%, 100%, 0.47),
    3px 3px 3px rgba(0, 0, 0, 0.2);
}
ul li {
  list-style: none;
  margin: 15px 0;
  padding: 10px;
  text-transform: capitalize;
  box-shadow: -5px -5px 5px hsla(0, 0%, 100%, 0.47),
    5px 5px 5px rgba(0, 0, 0, 0.13);
  -webkit-animation: opacity 0.2s linear;
  animation: opacity 0.2s linear;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
ul li i {
  color: teal;
  margin-left: 15px;
  cursor: pointer;
}
ul li input {
  background: transparent;
  border: none;
  outline: none;
  width: 70%;
  box-shadow: inset -2px -2px 2px hsla(0, 0%, 100%, 0.47),
    inset 2px 2px 2px rgba(0, 0, 0, 0.13);
}
h2 {
  text-align: center;
  color: teal;
}
.message {
  text-align: center;
  padding: 0.7rem;
  color: #e5e5e5;
}
.success-add {
  background-color: #008080;
}
.success-del {
  background-color: #c54440;
}
